.navbar{
	min-width: 500px;
	display: flex;
	flex-direction: row;
	background-color: #bebebe;
	border: solid black 2px;
}
.navbloc{
	padding: 10px 10px 10px 10px;
	border-right: solid black 1px;
	border-left: solid black 1px;
	
	text-decoration: none;
	font-weight: bold;
	color: black;
	width: 17%;
}

.navbloc:hover{
	background-color: rgb(255, 169, 255);
}

.active{
	background-color: rgb(214, 4, 214);
}