/*Common Page*/
.pads{
	padding: 0px 25px 25px 25px;
}

/* Home Page */
.card-column{
	display: flex;
	flex-direction: column;
}
.card-row{
	display: flex;
	flex-direction: row;
}

.cards{
	align-items: center;
}

.card{
	width:75%;
	min-width: 500px;
	padding: 10px;
	margin-bottom: 25px;

	border: black solid 3px;
	border-radius: 10px;
	background-color: #bebebe;

	color:black;
	font-size: large;
	font-weight: 600;
}
.card-info{
	width: 100%;
	padding: 0px 5px 0px 5px;
	
}
.card-title-link{
	justify-content: space-between;
	width: 100%;
}
.headline{
	text-align: center;
}
.contact{
	justify-content: space-between;
	margin: auto auto 50px auto;
	width: 50%;
}